body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Header {
  width: 100%;
  background-color: #C7EBDE;
  display: grid;
  grid-template-areas: "title buttons";
  grid-template-columns: 20vw 1fr;
  height: 8vh; }
  .Header .title-box {
    text-decoration: none;
    align-self: center; }
  .Header .title {
    grid-area: title;
    font-size: 2vw;
    margin-left: 5vw; }
  .Header .buttons {
    grid-area: buttons;
    align-self: center;
    margin-left: 80%; }

.intro {
  text-align: center;
  padding-top: 15vh;
  font-size: 1.5vw; }

.search-bar {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: grid;
  grid-template-areas: "form button";
  grid-template-columns: 1fr 10vw; }

.search-form {
  grid-area: form;
  width: 400px;
  margin-right: 20px; }

.search-button {
  grid-area: button; }

.login-field {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.signup-field {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.signup-input {
  width: 20vw; }


